.message {
  color: red;
  font-size: 20px;
  background-image: -webkit-gradient(linear, right top, left top, from(violet), color-stop(indigo), color-stop(blue), color-stop(green), color-stop(yellow), color-stop(orange), to(red));
  background-image: linear-gradient(to left, violet, indigo, blue, green, yellow, orange, red);
}

body{
  /*background:yellow;*/
  background: #d5d5d2;
  font-family: 'Montserrat', sans-serif;
}

@media (max-width: 800px) {
  /* CSS that should be displayed if width is equal to or less than 800px goes here */

  span {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

    font-size: 40px !important;
    letter-spacing:0.1em;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: white;
    /*text-shadow: 6px 6px #ff1f8f,*/
    text-shadow: 6px 6px #09d3ac,
              12px 12px #000000;
  }
}

@media (min-width: 800px) {
  /* CSS that should be displayed if width is equal to or less than 800px goes here */

  span {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

    font-size: 120px !important;
    letter-spacing:0.1em;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 3px;
    -webkit-text-stroke-color: white;
    text-shadow: 6px 6px #09d3ac,
              12px 12px #000000;
  }
}

